/* Top Level Components */
#section-tabs {
  position: sticky;
  top: 0;
}

#section-tabs button {
  min-width: 200px;
  text-align: start;
  color: #8497b8;
  font-weight: bold;
}

#section-tabs button:hover {
  background-color: #eef2f5;
}

#section-tabs button.active {
  color: #344767;
  background-color: #eef2f5;
}

table.dataTable th {
  color: #344767;
}

table.dataTable > tbody td {
  /* font-weight: 500; */
  color: #344767;
}

.request-status-options {
  display: flex;
  flex-direction: row;
  background-color: #f8f9fb;
  border-radius: 6px;
  padding: 5px;
}

.request-status-options button {
  color: #344767;
  background-color: transparent;
  border: none;
  padding: 7px 36px;
}

.request-status-options button.active {
  background-color: white;
  box-shadow: 0px 0px 3px #dfdfdf;
  /* border: 1px solid ;  */
  border-radius: 6px;
  border: 1px solid #dfdfdf;
}

/* New Request View */
#new-request-container {
  display: flex;
  flex-direction: row;
  gap: 1rem;
}

.new-request-ops-container {
  width: 100%;
  transition: width 0.45s ease-in;
}

.new-request-ops-container.service-selected {
  width: 400px;
  transition: width 0.45s ease-out;
  max-height: calc(100vh - 175px);
  overflow: auto;
}

.new-request-ops-container.service-selected .new-request-opts {
  flex-direction: column;
}

.new-request-opts {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(300px, 0.99fr));
  gap: 1.5rem;
  justify-content: start;
}

.new-request-opt {
  min-width: 300px;
  flex: 1 1 0px;
  cursor: pointer;
  background-color: #abadb9;
}

.new-request-opt:hover {
  filter: brightness(0.9);
}

.new-request-opt.active {
  background-color: #0b6ff8;
}

.new-request-opt.active::after {
  color: #0d6efd;
  border-left: solid 20px #0d6efd;
  border-top: solid 30px transparent;
  border-bottom: solid 30px transparent;
  transform: translateY(-50%);
  position: absolute;
  z-index: 1;
  content: "";
  top: 50%;
  left: 100%;
  height: 0;
  width: 0;
}

.new-request-opt .card-body {
  display: flex;
  gap: 0.5rem;
  align-items: center;
  color: white;
}

.new-request-opt .card-body h5 {
  margin: 0;
}
.new-request-opt .card-body i {
  color: #4a507b;
  border-radius: 6px;
  padding: 0.5rem;
  background-color: white;
}

#new-request-detail {
  position: sticky;
  top: 1rem;
  flex: 1;
}

/* Request Detail Components */
.request-body .detail-container {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  gap: 1rem;
}

.request-body .card.attachments {
  min-width: 340px;
  flex-grow: 1;
}

.request-body .card.attachments .form-actions {
  margin-top: auto;
}

.request-body .card.attachments .new-attachments {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
}

.request-body .detail {
  flex: 1 0 50%;
}

#request-body-service-type label {
  width: 100%;
}

.quick-actions-container {
  position: sticky;
  top: 0px;
  z-index: 100;
}

.quick-actions-container > div {
  position: absolute;
  display: flex;
  justify-content: end;
  left: -20px;
  width: 100%;
  z-index: 200;
}

.quick-actions-bar {
  height: 4rem;
  display: flex;
  flex-direction: row;
  gap: 1rem;
  justify-content: start;
  align-items: center;
  /* padding-right: 0.25rem; */
  color: white;
  /* background-color: #a7b3cd; */
  background: linear-gradient(145deg, #aab9d0, #8594ab);
  border-width: 0px;
}

