.quick-actions-bar .status-container .badge {
  max-width: 140px;
  overflow: hidden;
  text-overflow: ellipsis;
  vertical-align: bottom;
}

.quick-actions-bar .status-section-pause {
}

.quick-actions-bar .actions {
  height: 100%;
  display: flex;
  /* justify-content: end;
  align-items: stretch; */
}

.quick-actions-bar .actions button.action {
  height: 100%;
  margin: 0;
  border-radius: 0%;
}

.quick-actions-bar .actions .toggle-status {
  min-width: 0px;
  border-radius: 0%;
}

.quick-actions-bar .actions > button.action:last-of-type {
  border-radius: 0% 6px 6px 0%;
}

.quick-actions-bar button {
  /* flex-grow: 1;
  height: 95%;
